<template>
  <transition name="slide">
    <music-list :title="title" :father-image="bgImage" :songs="songs" :isRank="isRank"></music-list>
  </transition>
</template>

<script>
  import MusicList from '../music-list/music-list.vue'
  import {getRankDetail} from '../../api/rank'
  import {ERR_OK} from "../../api/config";
  import {mapGetters} from 'vuex'
  import {createSong} from "../../common/js/song"
  export default {
    data(){
      return{
        songs:[]
      }
    },
    created(){
      this.isRank=true;
      this.receiveRank()
    },
    computed:{
      //榜單名
      title() {
        return this.rank.topTitle
      },
      //榜單背景墻
      bgImage() {
        return this.rank.picUrl
      },
      ...mapGetters([
        'rank'
      ])
    },
    methods:{
      //接收榜单数据
      receiveRank(){
        if(!this.rank.id){
          this.$router.push({
            path: '/rank-page'
          });
          return;
        }
        getRankDetail(this.rank.id).then((res)=>{
          if (res.code === ERR_OK) {
            this.songs = this._normalizeSongs(res.songlist)
          }
          else {
            console.log("error")
          }
        })
      },
      //辅助处理数据
      //歌曲数据列表转换为歌曲类
      _normalizeSongs(list){
        let result=[];
        list.forEach((item)=>{
          if (item.data.songid && item.data.albummid) {
            result.push(createSong(item.data))
          }
        });
        return result
      }
    },
    components:{
      MusicList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s ease

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



